body {
   margin: 0;
   padding: 0;
   font-family: 'Helvetica', sans-serif;
   color: white;
}

.App {
   background: rgb(130, 226, 39);
   width: 100vw;
   height: 100vh;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;

   .clock {
      text-decoration: none;
      font-size: 100px;
      color: white;
      padding: 20px;
      border-radius: 50px;
      border: 10px solid white;
      transition: 0.5s;
      text-align: center;
      user-select: none;

      &:hover {
         transform: scale(1.02);
      }

   }

   .text {
      text-align: center;
   }
}

@media screen and (max-width: 550px) {
   .App {
      .clock {
         font-size: 80px;
      }
   }
}

@media screen and (max-width: 470px) {
   .App {
      .clock {
         font-size: 70px;
      }
   }
}

@media screen and (max-width: 420px) {
   .App {
      .clock {
         font-size: 60px;
      }
   }
}